.Home-video {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    z-index: -2;
    opacity: 50%
}

.Home-logo{
    position: relative;
    width: 500px;
    margin-top: 7%;
    z-index: -1;
}

.Home-link{
    font-size: 25px;
    font-family: 'Lexend Zetta', sans-serif;
    color: black;
}

@media only screen and (max-width: 768px) {
    .Home-logo{
        width: 330px;
        margin-top: 25%;
    }
}